.app {
  margin: 40px auto;
  width: 800px;
  height: 400px;
}
.playground {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: black;
}

.playground:after {
    content: '';
    display: block;
    position: absolute;
    top: 0px; left: 399px;
    height: 100%; width: 0px;
    border: dashed 1px whitesmoke;
}
.stick {
    position: absolute;
    width: 20px;
    height: 80px;
    background-color: whitesmoke;
}
.ball {
    position: absolute;
    width: 20px;
    height: 20px;
    background-color: whitesmoke;
}
body {
  margin: 0;
  padding: 0;
  font-family: Helvetica, sans-serif;
  background-color: #455A64;
}
